.root {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    overflow: hidden;
}

.gridList {
    -ms-flex-wrap: nowrap !important;
        flex-wrap: nowrap !important;
    /* Promote the list into his own layer on Chrome. This cost memory but helps keeping high FPS. */
    -webkit-transform: 'translateZ(0)';
            transform: 'translateZ(0)'
}

.gridList li {
    width: auto !important;
}

.gridListTile img{
    height: 100%;
    width: auto;
}

.gridListTile img:hover {
    cursor: pointer;
}

.title {
    color: #fff;
}

.titleBar {
    background: -webkit-gradient(linear, left bottom, left top, from(rgba(0,0,0,0.7)), color-stop(70%, rgba(0,0,0,0.3)), to(rgba(0,0,0,0)));
    background: -webkit-linear-gradient(bottom, rgba(0,0,0,0.7) 0%, rgba(0,0,0,0.3) 70%, rgba(0,0,0,0) 100%);
    background: -o-linear-gradient(bottom, rgba(0,0,0,0.7) 0%, rgba(0,0,0,0.3) 70%, rgba(0,0,0,0) 100%);
    background: linear-gradient(to top, rgba(0,0,0,0.7) 0%, rgba(0,0,0,0.3) 70%, rgba(0,0,0,0) 100%);
}



